.fade-out {
    opacity: 0;
    transition: opacity 5s ease;
}
@-webkit-keyframes fade-in {
    0% { opacity: 0; }
    100% { opacity: 1; }
}
@keyframes fade-in {
    0% { opacity: 0; }
    100% { opacity: 1; }
}
@-webkit-keyframes fade-out {
    99% { opacity: 1; }
    0% { opacity: 0; }
}
@keyframes fade-out {
    99% { opacity: 1; }
    0% { opacity: 0; }
}

