.grid-base{
    width: 34.9rem;
    height: 24.3rem;
    /*background: #D62D2D;*/
    /*background: #29a675;*/
    background: #25e5fffc;
    /*margin-right: 7rem;*/
    margin-bottom: -4rem;
    transform: rotateX(23deg);
    position: relative;
}
.projects-img{
    width: 34.9rem;
    height: 24.3rem;
    margin-top: -4vh;
    margin-left: 1.6vw;
    position: relative;
    z-index: -1;
    /*box-shadow: -11px 9px 9px 0 rgb(0 0 0 / 40%);*/
    /*box-shadow: 10px 10px 10px transparent;*/
    /*box-shadow: -5px 6px 0px 0px rgb(0 0 0 / 68%);*/
    box-shadow: -3px 5px 0px 0px rgb(0 0 0 / 68%);
}
.projects-img-overlay {
    width: 34.9rem;
    height: 39.99rem;
    background: black;
    transform: rotateX(52deg);
    margin-left: 0.5rem;
    margin-top: -32.6rem;
    padding: 1px;
    z-index: 9;
    /* border: 1px solid #38BDDF; */
    border: 1px solid #25e5fffc;
    transition: ease-out 0.3s;
    cursor: pointer;
    display: flex;
    justify-content: center;
}
.projects-img-overlay:hover{
    background-color: transparent;
    width: 34.7rem;
    height: 38.9rem;
    transform: rotateX(52deg);
    margin-left: 2.9rem;
    margin-top: -33.2rem;
    padding: 1px;
    z-index: 9;
    transition: ease-in-out 0.3s;
    box-shadow: -6px 10px 0 0 rgb(0 0 0 / 40%);
}
.projects-img-overlay:hover .project-text{
    color: transparent;
}
.base-container{
    /* transform: rotateY(45deg); */
    /*position: relative;*/
    height: 28rem;
    z-index: 8;
    /* transform: rotateY(-6deg); */
    /*transform: rotate3d(1, 1, 1, 18deg);*/
    /*transform: rotate3d(66, 14, 3, -25deg);*/
    /*transform: rotate3d(-8, 23, 97, 38deg);*/
    transform: rotate3d(-5, -48, 68, -22deg)
    /*transform: rotate3d(-8, 23, 97, -38deg);*/

}
