.project-anchor-area{
   /*margin-left: 5vw;*/
    box-sizing: content-box;
}

#devInfo{
    width: 100vw;
     height: 3vh;
    /* margin-bottom: 16vh; */
    display: block;
    justify-content: center;
    font: 2rem FontSiteSansBoldCondensed, Verdana, Helvetica, Arial, sans-serif;
    font-weight: 400;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
    /* color: #fff; */
    /* color: #38BDDF; */
    color: #25e5fffc;
    margin: 10rem;
    transform: rotateX(52deg);
    text-shadow: -1px 41px 8px;
    /* animation: fadeIn 5s; */
    overflow-wrap: normal;
}


#links{
    display: flex;
    /*justify-content: center;*/
    background: transparent;
    border: transparent;
    /* margin: 2px; */
    width: 13vw;
    justify-content: space-around;
    margin-top: -3rem;
}

.linkBtn{
    height: 4.5rem;
    width: auto;
    /* box-shadow: 0 0 20px 0 rgb(255 0 153 / 86%); */
    /* border-radius: 15px; */
    /* justify-content: space-between; */
    /* display: flex; */
    padding: 1rem;
}
.linkBtn:hover{
    transform: translate(0, -2px);
    /*box-shadow: 0 0 25px 0 hotpink;*/
    transition: 50ms ease;
}

#skills{
    width: 100vw;
    display: flex;
    justify-content: space-around;
    margin: 5rem;
    /* border: 1px solid white; */
    /* overflow: scroll; */
    /* position: relative; */
    height: 10vh;
    padding-right: 3vw;
}



/*#about-me{*/
/*    margin: 3rem;*/
/*}*/

.skills-img{
    height: 2rem;
    width: auto;
    margin-top: 3vh;
    transition: ease-out 0.3s;

}

.skills-img:hover{
    transform: translate(-1rem, -1rem);
    /* box-shadow: 0 0 25px 0 hotpink; */
    transition: ease-in-out 0.3s;
    height: 5rem;
    width: auto;
}

#devName{
    /*display: none;*/
}
#devTitle{
    /*display: none;*/
}

.fade-in {
    /*transition: opacity 2s ease;*/
    animation: fade-in 5s;
}
.fade-out {
    opacity: 0;
    transition: opacity 5s ease;
}
@keyframes fade-in {
    0% { opacity: 0; }
    100% { opacity: 1; }
}
@keyframes fade-out {
    99% { opacity: 1; }
    0% { opacity: 0; }
}

.img-container{
    /* position: absolute; */
    height: 5vh;
    width: 8.5vw;
    /* justify-content: space-evenly; */
    display: flex;
    margin-left: -0.1vw;
    margin-right: 0.289vw;
}
.project-title-text{
    overflow: visible;
    width: 16vw;
    height: 0vh;
    margin-left: -2vw;
}





